@charset "UTF-8";
html {
  box-sizing: border-box;
  background-color: #e5e5e5; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #011c40;
  background-color: #ffffff; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #141517; }

button {
  background-color: transparent;
  border: none;
  outline: none; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

.page-wrapper {
  margin: 0 auto; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .page-wrapper {
      margin: 0 auto; } }
  @media (max-width: 767px) {
    .page-wrapper {
      margin: 0 auto; } }

.page-header {
  margin: 0 auto;
  flex-wrap: wrap;
  align-items: center;
  background-color: #1e90ff;
  background-image: url("../img/intro-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  /* для резины
  @media (min-width: 1024px) and (max-width: 1700px) {
    padding: 30px 45px 30px 45px;
  }
  */ }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro-desktop@2x.jpg"); } }
  @media (min-width: 768px) and (max-width: 1023px) {
    .page-header {
      display: flex;
      flex-wrap: wrap;
      background-image: url("../img/intro-tablet@1x.jpg"); } }
  @media (min-width: 768px) and (max-width: 1023px) and (min-resolution: 144dpi), (min-width: 768px) and (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .page-header {
      display: flex;
      flex-wrap: wrap;
      background-image: url("../img/intro-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro-mobile@2x.jpg"); } }

.page-header__menu {
  padding-top: 35px;
  padding-bottom: 283px;
  padding-left: 7.5%;
  padding-right: 7.5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 210px; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .page-header__menu {
      width: 100%;
      padding: 0;
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .page-header__menu {
      width: 100%;
      padding-top: 20px;
      padding-left: 15px;
      padding-right: 15px; } }
  @media (max-width: 767px) {
    .page-header__menu--nojs {
      width: 100%;
      background-color: #ffffff;
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 45px;
      margin-bottom: 0; } }

.page-header__mobile-menu {
  display: none; }
  @media (max-width: 767px) {
    .page-header__mobile-menu {
      width: 50%;
      align-self: flex-end;
      order: 2;
      display: flex;
      align-items: center;
      justify-content: flex-end; } }

.page-header__intro {
  color: #ffffff;
  padding-left: 95px; }
  .page-header__intro h1 {
    font-size: 54px;
    line-height: 66px;
    font-weight: 700; }
  .page-header__intro p {
    font-size: 20px; }

@media (max-width: 767px) {
  .page-header__burger-menu {
    display: none; } }

@media (max-width: 767px) {
  .site-nav {
    display: block;
    width: 100%;
    order: 3; }
    .site-nav--hide {
      display: none; }
    .site-nav--show {
      display: block; } }

.site-nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  max-width: 1060px;
  flex-wrap: wrap;
  margin: 0;
  padding-top: 20px; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .site-nav ul {
      max-width: 570px; } }
  @media (max-width: 767px) {
    .site-nav ul {
      display: block; } }
  .site-nav ul--nojs {
    display: block; }

.site-nav li {
  padding-bottom: 20px;
  margin-right: 33px; }
  .site-nav li:last-child {
    margin-right: 0; }

.site-nav a {
  color: #ffffff;
  line-height: 20px;
  font-weight: 500; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .site-nav a {
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .site-nav a {
      font-size: 18px;
      line-height: 22px;
      color: #011c40; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .logo img {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .logo img {
    width: 42px;
    height: 22px; } }

@media (max-width: 767px) {
  .logo--white {
    display: none; } }

.logo--blue {
  display: none; }
  @media (max-width: 767px) {
    .logo--blue {
      display: block;
      width: 50%;
      align-self: flex-start;
      order: 1; } }
